<template>
    <home-container>
        <ion-toolbar>
            <ion-text slot="start"><h2 class='h2 ion-margin' style="font-weight:bolder">Jackye Madu<br><small style='font-size:1rem' class='text-muted'>@JackyeDBoss</small></h2>  </ion-text>
            <ion-buttons slot='end'>
                <ion-button color='dark' @click="router.push('/home/messages')">
                    <ion-icon slot="icon-only" :icon="notificationsOutline"></ion-icon>
                </ion-button>
            </ion-buttons>
        </ion-toolbar>
        <debit-card></debit-card>
        <ion-button color='dark' strong size='small' expand='block' fill='clear' class='rounded ion-margin-horizontal my-3'  style='border:1px dashed #353535'><ion-icon slot='start' :icon='trashOutline'></ion-icon> Remove</ion-button>

        <ion-list lines="none">
            <ion-item @click.prevent="router.push(i.link)" v-for="(i,index) in menu" :key="index"  mode="md"  class='ion-margin-bottom card' button :detail="false">
                <ion-icon slot='start' class='h1' :icon="i.icon"></ion-icon>
                <ion-label slot='start'>{{i.title}}</ion-label>
            </ion-item>
        </ion-list>
        <ion-button @click="handleLogout" class='ion-padding-horizontal logout-btn' expand="block">
            <ion-icon  slot="start" :icon="powerOutline"></ion-icon>
            <ion-label>Log Out</ion-label>
        </ion-button>
    </home-container>
</template>
<script>
import { defineComponent } from "vue";
import { useRouter } from "vue-router";
import DebitCard from "@/views/components/DebitCard.vue";
import { loadingController } from "@ionic/vue";
import { notificationsOutline,diamondOutline,personCircleOutline,settingsOutline,lockClosedOutline,powerOutline,trashOutline} from "ionicons/icons";
    export default defineComponent({
        components:{DebitCard},
        data() {
            return {
                menu:[
                    {icon:diamondOutline,title:'Investment',link:'/home/account/investments'},
                    {icon:notificationsOutline,title:'Notification',link:'/home/account/notification'},
                    {icon:personCircleOutline,title:'Profile',link:'/home/account/profile'},
                    {icon:settingsOutline,title:'Setings',link:'/home/account/settings'},
                    {icon:lockClosedOutline,title:'Security',link:'/home/account/security'},
                ]
            }
        },
        setup() {
            const router = useRouter();
            return {
            diamondOutline,
            personCircleOutline,
            notificationsOutline,
            settingsOutline,
            lockClosedOutline,powerOutline,trashOutline,router
            };
        },
        methods:{
            async handleLogout(){
                let loading = await loadingController.create({
                    message: "Please wait...",
                });
                loading.present();
                setTimeout(() => {
                    this.router.push('/lock-screen');
                    loading.dismiss();
                }, 1000);
            },
        }
});

</script>
<style scoped>
.logout-btn{
    --background:#2b2335;
    color:#B582F4;
    --border-width:1px;
    --border-style:solid;
    --border-color:#B582F4;
}
</style>